<template>
  <div class="page">
    <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    title="我的二维码"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content qk-my-card">
      <div class="avatar"><img :src="user_profiles.avatar_url" alt=""></div>
      <div class="user-name" v-if="user_profiles.nickname">{{user_profiles.nickname}}</div>
      <div class="user-name" v-if="!user_profiles.nickname">暂无昵称</div>
      <div class="qrcode">
        <div class="across-space top"></div>
        <div class="vertical-space right"></div>
        <div class="across-space bottom"></div>
        <div class="vertical-space left"></div>
        <img :src="user_profiles.qrcode_url">
      </div>
      <div class="remind">扫一扫加入钱咖</div>
    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .qk-my-card {
    padding-top: 110px;
    background: #FFFFFF;
    text-align: center;

    .avatar {
      margin-bottom: 1px;

      img {
        width: 44px;
        height: 44px;
        border-radius: 50%;
      }
    }

    .user-name {
      font-size: 13px;
      height: 18px;
      line-height: 18px;
      margin-bottom: 29px;
      color: #3A3A3A;

      @include font-semibold();
    }

    .qrcode {
      position: relative;
      margin: 0 auto;
      padding: 3px;
      width: 186px;
      height: 186px;
      border: 2px solid #DDDDDD;
      margin-bottom: 30px;

      img {
        width: 176px;
        height: 176px;
        -webkit-touch-callout: default;
      }

      .across-space {
        position: absolute;
        width: 166px;
        height: 2px;
        background-color: #FFFFFF;

        &.top {
          top: -2px;
          left: 8px;
        }

        &.bottom {
          bottom: -2px;
          left: 8px;
        }
      }

      .vertical-space {
        position: absolute;
        width: 2px;
        height: 166px;
        background-color: #FFFFFF;

        &.right {
          right: -2px;
          top: 8px;
        }

        &.left  {
          left: -2px;
          top: 8px;
        }
      }
    }

    .remind {
      height: 18px;
      font-size: 13px;
      line-height: 18px;
      color: #b5b5b5;
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {mapState, mapActions} from 'vuex'

  export default {
    components: {
      AnimateHeader
    },

    computed: {
      ...mapState('user', [
        'user_profiles'
      ])
    },

    mounted () {
      this.getProfile()
    },

    methods: {
      back () {
        $router.back('/user/profile')
      },

      ...mapActions('user', [
        'getProfile'
      ])
    }
  }
</script>



